<template>
  <div class="compApply">
    <page-content>

      <!-- <van-pull-refresh v-model="isLoading" @refresh="onRefresh"> -->

      <div class="content-padded">

        <headerThree :message='message'></headerThree>

        <div class="yh-body" @touchmove="touchmove($event)" @scroll="scroll($event)">
          <span class="span_content" v-show="contentShowA" @touchmove="touchmove($event)" @scroll="scroll($event)">
            <van-radio-group v-model="applyType" @change="applyTypeChange" v-if="!isQuery">
              <span class="radio_span">
                <van-radio name="1"></van-radio>
              </span>
              <span class="radio_span">
                <van-radio name="2"></van-radio>
              </span>
            </van-radio-group>
            <van-field class="yh-my-blue" required left-icon="location-o" v-model="tableData.groupName" @click="showAddressChuang" readonly label="" placeholder="" :right-icon="icon" clearable size="large" />
            <van-field class="yh-my-red" required left-icon="user-o" v-model="tableData.ctmName" :readonly="isReadyonly" clearable label="" :placeholder="isReadyonly?'-':''" size="large" @blur="blur(tableData.ctmName,'ctmName')"/>
            <van-field class="yh-my-blue" required left-icon="idcard" v-model="tableData.ctmIdCard" :readonly="isReadyonly" clearable oninput="this.value=this.value.replace(/[^0-9Xx]+/,'');" label="" :placeholder="isReadyonly?'-':''" size="large" @blur="blur(tableData.ctmIdCard,'ctmIdCard')"/>
            <van-field class="yh-my-red" required left-icon="wap-home-o" v-model="tableData.userAddress" :readonly="isReadyonly" clearable label="" :placeholder="isReadyonly?'-':''" size="large" @blur="blur(tableData.userAddress,'userAddress')"/>
            <van-field class="yh-my-blue" required left-icon="contact" v-model="tableData.operator" :readonly="isReadyonly" clearable label="" :placeholder="isReadyonly?'-':''" size="large" @blur="blur(tableData.operator,'operator')"/>
            <van-field class="yh-my-red" required left-icon="phone-o" v-model="tableData.operatorPhone" :readonly="isReadyonly" clearable label="" :placeholder="isReadyonly?'-':''" type="number" oninput="this.value=this.value.replace(/[^0-9]+/,'');" size="large" @blur="blur(tableData.operatorPhone,'operatorPhone')"/>

            <van-field class="yh-my-red" required left-icon="bar-chart-o" v-model="tableData.predictWater" :readonly="isReadyonly" clearable type="number" label="" :placeholder="isReadyonly?'-':''" size="large" @blur="blur(tableData.predictWater,'predictWater')"/>
            <van-field class="yh-my-blue" required left-icon="friends-o" v-model="tableData.predictUser" :readonly="isReadyonly" clearable type="number" label="" :placeholder="isReadyonly?'-':''" size="large" @blur="blur(tableData.predictUser,'predictUser')"/>
            <van-field class="yh-my-blue" required left-icon="orders-o" v-model="tableData.applyCount" :readonly="isReadyonly" clearable label="" type="number" :placeholder="isReadyonly?'-':''" size="large" @blur="blur(tableData.applyCount,'applyCount')"/>
            <van-field class="yh-my-red"  left-icon="comment-o" v-model="tableData.applyReason" :readonly="isReadyonly" clearable label="" :placeholder="isReadyonly?'-':''" size="large"/>
            <!-- <van-button type="info" class="next" @click="nextA" v-if="btnShow" style="margin-top: 10px;"></van-button> -->
            <div class="submit_box" v-if="btnShow">
              <!-- !($route.query.isModify == 0) && -->
              <van-button type="info" class="submit_btn" @click="submit(1)" v-if="!($route.query.isModify == 0) && isShowBtn"> </van-button>
              <van-button type="info" class="submit_btn" @click="nextA" v-if="btnShow"></van-button>
            </div>
            <div class="yh-footer" v-if="btnShow">
              <div class="yh-info" @click="takeInfo()">
                <img src="../../../../../static/img/shouzhi(1).png" alt="">
                <span class="kr-span kr-info"></span>
                <!-- <van-icon v-if="!isShowInfo" name="arrow"/>
                <van-icon v-if="isShowInfo" name="arrow-down"/> -->
              </div>
              <div v-if="isShowInfo">
                <span class="kr-span"></span>
                <span class="kr-span">0774-38234020774-3956000</span>
                <span class="kr-span">yhjhyyb@163.com</span>
                <span class="kr-span"></span>
                <span class="kr-span"></span>
                <span class="kr-span"></span>
                <span class="kr-span"></span>
              </div>
            </div>
          </span>

          <span class="span_content" v-show="contentShowB" @touchmove="touchmove($event)" @scroll="scroll($event)">
            <div class="top_title">&nbsp;</div>
            <!-- <van-field 
                    v-model="bank.accountType" 
                    :readonly="isReadyonly" 
                    label="" 
                    placeholder="" 
                    size="large" />
            <van-field 
                    v-model="bank.payType" 
                    :readonly="isReadyonly" 
                    label="" 
                    placeholder="" 
                    size="large" /> -->
            <van-field class="yh-my-blue" required left-icon="shop-o" v-model="bank.openBank" :readonly="isReadyonly" label="" clearable :placeholder="isReadyonly?'-':''" size="large" @blur="blur(bank.openBank,'openBank')"/>
            <van-field class="yh-my-red" required left-icon="user-circle-o" v-model="bank.accountName" :readonly="isReadyonly" clearable label="" :placeholder="isReadyonly?'-':''" size="large" @blur="blur(bank.accountName,'accountName')"/>
            <van-field class="yh-my-blue" required left-icon="contact" v-model="bank.bankNo" :readonly="isReadyonly" label="" clearable :placeholder="isReadyonly?'-':''" size="large" @blur="blur(bank.bankNo,'bankNo')"/>
            <!-- <div class="bottomBtn_content">
              <van-button type="info" class="next" style="float: left;margin-left: 3%" @click="goBackB" v-if="btnShow"></van-button>
              <van-button type="info" class="next" @click="nextB" v-if="btnShow"></van-button>
            </div> -->
            <div class="submit_box" v-if="btnShow">
              <van-button type="info" class="submit_btn" @click="goBackB" v-if="btnShow"></van-button>
              <van-button type="info" class="submit_btn" @click="submit(1)" v-if="!($route.query.isModify == 0)"> </van-button>
              <van-button type="info" class="submit_btn" @click="nextB" v-if="btnShow"></van-button>
            </div>
          </span>

          <span class="span_content" v-show="contentShowC" @touchmove="touchmove($event)" @scroll="scroll($event)">
            <div class="top_title">&nbsp;</div>
            <van-field class="yh-my-blue" required left-icon="label-o" v-model="invoice.invoiceTypeName" readonly clearable label="" :placeholder="isReadyonly?'-':''" size="large" :right-icon="icon" @click="selectRelation()" @click-right-icon="selectRelation()" />
            <van-field class="yh-my-red" required left-icon="shop-collect-o" v-model="invoice.unitName" :readonly="isReadyonly" clearable label="" :placeholder="isReadyonly?'-':''" size="large" @blur="blur(invoice.unitName,'unitName')"/>
            <van-field class="yh-my-blue" required left-icon="user-circle-o" v-model="invoice.identifiNum" :readonly="isReadyonly" clearable label="" :placeholder="isReadyonly?'-':''" size="large" @blur="blur(invoice.identifiNum,'identifiNum')"/>
            <van-field class="yh-my-red" required left-icon="phone-o" v-model="invoice.mobile" :readonly="isReadyonly" clearable label="" :placeholder="isReadyonly?'-':''" type="number" oninput="this.value=this.value.replace(/[^0-9]+/,'');" size="large" @blur="blur(invoice.mobile,'mobile')"/>
            <van-field class="yh-my-blue" required left-icon="shop-o" v-model="invoice.invOpenBank" :readonly="isReadyonly" clearable label="" :placeholder="isReadyonly?'-':''" size="large" @blur="blur(invoice.invOpenBank,'invOpenBank')"/>
            <van-field class="yh-my-red" required left-icon="contact" v-model="invoice.invAccountNo" :readonly="isReadyonly" clearable label="" :placeholder="isReadyonly?'-':''" size="large" @blur="blur(invoice.invAccountNo,'invAccountNo')"/>
            <van-field class="yh-my-blue" required left-icon="location-o" v-model="invoice.contactAddr" :readonly="isReadyonly" clearable label="" :placeholder="isReadyonly?'-':''" size="large" @blur="blur(invoice.contactAddr,'contactAddr')"/>
            <!-- <div class="bottomBtn_content">
              <van-button type="info" class="next" style="float: left;margin-left: 3%" @click="goBackC" v-if="btnShow"></van-button>
              <van-button type="info" class="next" @click="nextC" v-if="btnShow"></van-button>
            </div> -->
            <div class="submit_box" v-if="btnShow">
              <van-button type="info" class="submit_btn" @click="goBackC" v-if="btnShow"></van-button>
              <van-button type="info" class="submit_btn" @click="submit(1)" v-if="!($route.query.isModify == 0)"> </van-button>
              <van-button type="info" class="submit_btn" @click="nextC" v-if="btnShow"></van-button>
            </div>
          </span>

          <span class="span_content" v-show="contentShowD" @touchmove="touchmove($event)" @scroll="scroll($event)">
            <div v-if="PhotoShow1">
              <div class="upLoad_title"><span style="color: red">*</span></div>
              <van-uploader v-model="fileList1" :max-count="1" :deletable='!isReadyonly' />
            </div>

            <div v-if="PhotoShow2">
              <div class="upLoad_title"><span style="color: red">*</span></div>
              <van-uploader v-model="fileList2" :max-count="1" :deletable='!isReadyonly' />
            </div>

            <div v-if="PhotoShow3">
              <div class="upLoad_title"><span style="color: red">*</span></div>
              <van-uploader v-model="fileList3" :max-count="1" :deletable='!isReadyonly' />
            </div>

            <div v-if="bankPhotoShow">
              <div class="upLoad_title"><span style="color: red">*</span></div>
              <van-uploader v-model="fileList4" :max-count="1" :deletable='!isReadyonly' />
            </div>

            <!-- <van-button type="info" class="next" style="float: left;margin-left: 3%;margin-top: 10px" @click="goBackD" v-if="btnShow"></van-button>
            <van-button type="info" class="next" style="margin-top: 10px" @click="nextD" v-if="btnShow"></van-button> -->
            <div class="submit_box" v-if="btnShow">
              <van-button type="info" class="submit_btn" @click="goBackD" v-if="btnShow"></van-button>
              <van-button type="info" class="submit_btn" @click="getImgBase64(1)" v-if="!($route.query.isModify == 0)"> </van-button>
              <van-button type="info" class="submit_btn" @click="nextD" v-if="btnShow"></van-button>
            </div>
          </span>

          <div class="submit_box" v-if="editShow">
            <van-button type="info" class="submit_btn" @click="goBackE" v-if="!btnShow && !($route.query.isModify == 0)"></van-button>
            <van-button type="info" class="submit_btn" @click="getImgBase64(1)" v-if="!btnShow && !($route.query.isModify == 0)"></van-button>
            <van-button type="info" class="submit_btn" @click="getImgBase64(0)" v-if="!btnShow && !($route.query.isModify == 0)"></van-button>
          </div>
          <div v-else>
            <van-button type="info" class="next" style="margin-top: 10px" @click="modify"></van-button>
          </div>
        </div>

      </div>

      <!-- </van-pull-refresh> -->

    </page-content>

    <!--  -->
    <van-popup position="bottom" :style="{ height: '40%' }" v-model="relationShow">
      <van-area :area-list="areaList" :value="areaNum" @confirm="sure" @cancel="exit" />
    </van-popup>
    <!--  -->
    <van-popup position="bottom" :style="{ height: '40%' }" v-model="invoiceTypeShow">
      <van-picker class="timepicker" show-toolbar title="" :columns="invoiceTypeNameList" @cancel="onCancelRelation" @confirm="onConfirmRelation" />
    </van-popup>
  </div>

</template>

<script>
import myHeader from '@/comments/myHeader'
import headerThree from '@/comments/headerThree';
import axios from 'axios'
import { Toast, PullRefresh, Dialog } from 'vant';
export default {
  components: {
    myHeader, headerThree
  },
  data() {
    return {
      isShowBtn:true,//

      isPeopleShow: 1,//0 1
      isQuery:false,//
      message: {
        title: "",
        span1: ",",
        span2: "",
        isShow: '',
      },
      applyType: '2',
      isShowInfo: false,
      //
      isLoading: false,
      scrollTop: 0,//

      invoiceTypeShow: false,
      invoiceTypeList: [],
      invoiceTypeNameList: [],
      icon: 'arrow-down',
      iconType: 'arrow',
      PhotoShow1: true,
      PhotoShow2: true,
      PhotoShow3: true,
      bankPhotoShow: false,
      title: '',
      contentShowA: true,
      contentShowB: false,
      contentShowC: false,
      contentShowD: false,
      btnShow: true,
      bankShow: false,
      invoiceShow: false,
      bank: {
        openBank: '',
        accountName: '',
        bankNo: '',
      },
      invoice: {
        invoiceType: '',
        invoiceTypeName: '',
        unitName: '',
        identifiNum: '',
        mobile: '',
        invOpenBank: '',
        invAccountNo: '',
        contactAddr: '',
      },
      tableData: {
        groupName: '',
        ctmName: '',
        operator: '',
        operatorPhone: '',
        applyCount: '',
        predictWater: '',
        predictUser: '',
        ctmIdCard: '',
        userAddress: '',
      },
      fileList1: [],
      fileList2: [],
      fileList3: [],
      fileList4: [],
      fileList: [],
      fileUrlList: [],
      initFileList: [],
      isReadyonly: false,
      ftoken: '',
      editShow: true,

      // 
      relationShow: false,
      //  
      //   
      showAddress: false,
      // code
      areaNum: "01013",
      // 
      areaList: {},
      emailURL: '',
    }
  },
  mounted() {
    console.log(this.$route.query.busiType);
    //   
    if(this.$route.query.isDraft == '1' || this.$route.query.busiType == undefined){
      this.isShowBtn = true;
    }else{
      this.isShowBtn = false;
    }

    // bug
    document.body.addEventListener('focusout', () => {
      window.scrollTo({ top: 0, left: 0, behavior: 'smooth' })
    })
    this.emailURL = URLurl;
    if (this.$route.query.isModify == 0) {// 
      this.isReadyonly = true
      this.icon = '';
      this.iconType = '';
      this.init()
      this.title = ''
      this.contentShowA = true
      this.contentShowB = true
      this.contentShowC = true
      this.contentShowD = true
      this.btnShow = false

      this.isQuery = true;//
    } else if (this.$route.query.isModify == 1) {// 
      // this.isReadyonly = false
      // this.init()

      this.isReadyonly = true
      this.icon = '';
      this.iconType = '';
      this.init()
      this.title = ''
      this.contentShowA = true
      this.contentShowB = true
      this.contentShowC = true
      this.contentShowD = true
      this.btnShow = false
      this.editShow = false

      this.isQuery = true;//
    } else if (this.$route.query.isModify == 2) {// 
      this.isReadyonly = false
      this.getArrList2()

      this.isQuery = false;//
    }
  },
  watch: {
    // fileUrlList: {
    //   handler(newValue, oldValue) {
    //     if (this.bankPhotoShow) {
    //       if (newValue.length == 4) {
    //         this.submit()
    //       }
    //     } else {
    //       if (newValue.length == 3) {
    //         this.submit()
    //       }
    //     }
    //   },
    //   deep: true
    // },
  },
  methods: {
    blur(val,text){//
      let message = '';
      if(val === ''){
        switch (text) {
          case 'ctmName':
            message = ""
            break;
          case 'ctmIdCard':
            message = ""
            break;
          case 'userAddress':
            message = ""
            break;
          case 'operator':
           message = ''
           break
          case 'operatorPhone':
            message = ""
            break;
          case 'predictWater':
            message = ""
            break;
          case 'predictUser':
            message = ''
            break;
          case 'applyCount':
            message = ''
            break

          case 'openBank':
            message = ""
            break;
          case 'accountName':
            message = ""
            break;
          case 'bankNo':
            message = ""
            break;
            
          // case 'invoiceTypeName':
          //   message = ""
          //   break;
          case 'unitName':
            message = ""
            break;
          case 'identifiNum':
            message = ""
            break;
          case 'mobile':
            message = ""
            break;
          case 'invOpenBank':
            message = ""
            break;
          case 'invAccountNo':
            message = ""
            break;
          case 'contactAddr':
            message = ""
            break;
          default:
          
            break;
        }
        Toast(message);
      }else {
        if(text == 'ctmIdCard' && !/^[1-9][0-9]{5}(19|20)[0-9]{2}((01|03|05|07|08|10|12)(0[1-9]|[1-2][0-9]|31)|(04|06|09|11)(0[1-9]|[1-2][0-9]|30)|02(0[1-9]|[1-2][0-9]))[0-9]{3}([0-9]|x|X)$/.test(val)){
          message = '!';
          Toast(message);
        }else if(text == 'operatorPhone' && !(/^1[3456789]\d{9}$/.test(this.tableData.operatorPhone))){
          message = '!';
          Toast(message);
        }else if(text == 'mobile' && !(/^1[3456789]\d{9}$/.test(this.invoice.mobile))){
          message = '!';
          Toast(message);
        }else if (text == 'predictUser') {
          // if ( parseInt(val) > 1000000) {
          //   message = '!';
          //   Toast(message);
          //   this.tableData.predictUser = '';
          // }else 
          if(val == 0){
            message = '0!';
            Toast(message);
            this.tableData.predictUser = '';
          }
        }else if (text == 'predictWater') {
          // if ( parseInt(val) > 100000) {
          //   message = '!';
          //   Toast(message);
          //   this.tableData.predictWater = '';
          // }else 
          if(val == 0){
            message = '0!';
            Toast(message);
            this.tableData.predictWater = '';
          }
        }else if (text == 'applyCount') {
          // if ( parseInt(val) > 100000) {
          //   message = '!';
          //   Toast(message);
          //   this.tableData.applyCount = '';
          // }else
          if(val == 0){
            message = '0!';
            Toast(message);
            this.tableData.applyCount = '';
          }
        }
      }
    },
    // 
    // applyTypeChange(applyType) {
    //   if (applyType == '1') {
    //     let item = {}
    //     item.isModify = '2'
    //     this.$router.push({ path: '/peopleApply', query: item })
    //   }
    // },
    applyTypeChange(name) {
      this.isPeopleShow = name;
      this.$emit('isPeopleShow', this.isPeopleShow);
    },
    takeInfo() {
      if (!this.isShowInfo) {
        this.isShowInfo = true;
      } else {
        this.isShowInfo = false;
      }
    },
    scroll(e) {
      if ((parseInt(e.target.clientHeight) + Math.ceil(e.target.scrollTop)) === parseInt(e.target.scrollHeight)) {
        // console.log("")
      } else if (e.target.scrollTop == 0) {
        // console.log("")
        e.stopPropagation()
      }
      this.$set(this, 'scrollTop', e.target.scrollTop)
    },
    touchmove(e) {
      if (this.scrollTop !== 0) {
        e.stopPropagation()//
      }
    },
    onRefresh() {//
      setTimeout(() => {
        this.$toast('');
        this.isLoading = false;
        this.emailURL = URLurl;
        if (this.$route.query.isModify == 0) {// 
          this.isReadyonly = true
          this.icon = '';
          this.iconType = '';
          this.init()
          this.title = ''
          this.contentShowA = true
          this.contentShowB = true
          this.contentShowC = true
          this.contentShowD = true
          this.btnShow = false
        } else if (this.$route.query.isModify == 1) {// 
          this.isReadyonly = false
          this.init()
        } else if (this.$route.query.isModify == 2) {// 
          this.isReadyonly = false
          this.getArrList2()
        }
      }, 500);
    },

    getInvoiceType() {//
      let _this = this
      let params = {
        "data": ['invoice'],
      }
      _this.$api.fetch({
        params,//
        apiUrl: '/common/dictDownList',//
      }).then(res => {
        _this.invoiceTypeList = res.invoice;
        for (let i = 0; i < res.invoice.length; i++) {
          _this.invoiceTypeNameList.push(res.invoice[i].name)
        }
        if (_this.$route.query.isModify != 2) {
          for (let i = 0; i < _this.invoiceTypeList.length; i++) {
            if (_this.invoice.invoiceType == _this.invoiceTypeList[i].value) {
              _this.invoice.invoiceTypeName = _this.invoiceTypeList[i].name
            }

          }
        }
      })
    },
    //
    selectRelation() {
      this.invoiceTypeShow = true;
    },
    // 
    onCancelRelation() {
      this.relationShow = false;
    },
    //
    onConfirmRelation(value, index) {
      this.invoice.invoiceTypeName = value;
      for (let i = 0; i < this.invoiceTypeList.length; i++) {
        if (value == this.invoiceTypeList[i].name) {
          this.invoice.invoiceType = this.invoiceTypeList[i].value;
        }
      }
      this.invoiceTypeShow = false;
    },
    // 
    init() {
      var _this = this;
      let params = {//
        "data": {
          "groupCode": this.$route.query.groupCode,
          "busiId": this.$route.query.busiId,
          "busiType": this.$route.query.busiType,
          "source": this.$route.query.source,
          // "isModify": this.$route.query.isModify,
        },
      }
      this.$api.fetch({
        params: params,//
        apiUrl: '/myWater/busiQuery',//
      }).then(res => {
        _this.tableData = res.data
        _this.tableData.groupCode = res.groupCode
        _this.tableData.groupName = res.groupName
        if ('bank' in res.data) {
          if (res.data.bank !== null) {
            _this.bank = res.data.bank
            _this.bankShow = true
          } else {
            _this.contentShowB = false
            _this.bankPhotoShow = false
          }
        } else {
          _this.contentShowB = false
          _this.bankPhotoShow = false
        }
        if ('invoice' in res.data) {
          if (res.data.invoice !== null) {
            _this.invoice = res.data.invoice
            _this.invoiceShow = true
          } else {
            _this.contentShowC = false
          }
        } else {
          _this.contentShowC = false
        }
        // _this.getWater()
        _this.getArrList2()
        // _this.fileList1.push({ url: _this.tableData.accessory[0].url })
        // _this.fileList2.push({ url: _this.tableData.accessory[1].url })
        // _this.fileList3.push({ url: _this.tableData.accessory[2].url })
        if (_this.tableData.accessory.length >= 1) {
          _this.PhotoShow1 = true
          _this.fileList1.push({ url: _this.tableData.accessory[0].url })
        } else {
          _this.PhotoShow1 = false
        }
        if (_this.tableData.accessory.length >= 2) {
          _this.PhotoShow2 = true
          _this.fileList2.push({ url: _this.tableData.accessory[1].url })
        } else {
          _this.PhotoShow2 = false
        }
        if (_this.tableData.accessory.length >= 3) {
          _this.PhotoShow3 = true
          _this.fileList3.push({ url: _this.tableData.accessory[2].url })
        } else {
          _this.PhotoShow3 = false
        }
        if (_this.tableData.accessory.length >= 4) {
          _this.bankPhotoShow = true
          _this.fileList4.push({ url: _this.tableData.accessory[3].url })
        }
        _this.initFileList = res.data.accessory
        // for (let i = 0; i < this.tableData.accessory.length; i++) {
        //   _this.fileList.push(this.tableData.accessory[i].url)
        // }
        _this.getInvoiceType()
      })
    },
    // getImgBase64 > getFtoken > getImgUrl > submit
    getImgBase64(isSave) {
      if ('url' in this.fileList1[0]) {// 
        this.fileList.push(this.fileList1[0].url) // 
      } else {// 
        this.fileList.push(this.fileList1[0].file)
      }

      if ('url' in this.fileList2[0]) {// 
        this.fileList.push(this.fileList2[0].url) // 
      } else {// 
        this.fileList.push(this.fileList2[0].file)
      }

      if ('url' in this.fileList3[0]) {// 
        this.fileList.push(this.fileList3[0].url) // 
      } else {// 
        this.fileList.push(this.fileList3[0].file)
      }

      // 
      if (this.bankPhotoShow) {
        if ('url' in this.fileList4[0]) {// 
          this.fileList.push(this.fileList4[0].url) // 
        } else {// 
          this.fileList.push(this.fileList4[0].file)
        }
      }
      //isSave 
      this.getFtoken(isSave)
    },
    // 
    modify() {
      this.isReadyonly = false
      this.title = ''
      this.contentShowA = true
      this.contentShowB = false
      this.contentShowC = false
      this.contentShowD = false
      this.btnShow = true
      this.PhotoShow1 = true
      this.PhotoShow2 = true
      this.PhotoShow3 = true

      this.editShow = true
      this.icon = 'arrow-down';
    },
    // ftoken
    getFtoken(isSave) {
      var _this = this;
      let params = {//
        "data": {},
      }
      this.$api.fetch({
        params: params,//
        apiUrl: '/myWater/getFtoken',//
      }).then(res => {
        _this.ftoken = res.ftoken
        // ftokenurl
        if (_this.$route.query.isModify == 2) {// 
          for (let i = 0; i < _this.fileList.length; i++) {
            _this.getImgUrl(_this.fileList[i], i, isSave)
          }
        } else {// 
          // getImgUrl()submit()submit()
          var goSubmit = 0
          for (let i = 0; i < _this.fileList.length; i++) {
            if (typeof (_this.fileList[i]) == 'string') {
              _this.fileUrlList[i] = _this.fileList[i]
              goSubmit++
            } else {
              _this.getImgUrl(_this.fileList[i], i, isSave)
            }
          }
          if (goSubmit == _this.fileList.length) {
            _this.submit(isSave)
          }
        }
      })
    },
    // 
    getImgUrl(item, i, isSave) {
      var _this = this;
      let $formData = new FormData();
      $formData.append('file', item);
      $formData.append('ftoken', this.ftoken);
      // 
      // 
      let RequestObj = {
        // url: 'http://10.13.1.248:10041/fileService/fileUpload',//
        // url: 'http://219.139.169.234:9992/fileService/fileUpload',//
        url: _this.emailURL,
        method: 'post',
        timeout: 60000,
        headers: { 'Content-Type': 'application/json' },
        withCredentials: true,
        data: $formData
      };
      axios(RequestObj).then(res => {
        if (res.status === 200) {
          if (res.data.code === 0) {

            _this.fileUrlList[i] = res.data.data.path
            if (_this.fileUrlList.length == _this.fileList.length) {
              _this.submit(isSave)
            }

            // if (_this.$route.query.isModify == 2) {// 
            //   _this.fileUrlList.push(res.data.data.path)
            // } else {// 
            //   _this.fileUrlList[i] = res.data.data.path
            //   if (_this.fileUrlList.length == _this.fileList.length) {
            //     _this.submit()
            //   }
            // }
          }
        }
      }, err => {

      }).catch(error => {

      })
    },
    // 
    moduleAccess(groupCode) {
      var _this = this;
      let params = {//
        "data": {
          "groupCode": groupCode,
          "openId": localStorage.getItem('openId'),
          'moduleId': '4',
        },
      }
      this.$api.fetch({
        params: params,//
        apiUrl: '/common/moduleVisitAdd',//
      }).then(res => {

      })
    },
    // 
    submit(isSave) {
      var _this = this;
      if (this.tableData.groupCode === '') {
        Toast('');
        return
      }
      if (this.tableData.ctmName === '') {
        Toast('');
        return
      }
      // _this.fileUrlList[undefined, undefinedxxxxxx.jpg],bug
      for (let i = 0; i < _this.fileUrlList.length; i++) {
        if (_this.fileUrlList[i] === undefined) {
          return
        }
      }

      let params = {//
        "data": {
          "isSave": isSave,
          "groupCode": this.tableData.groupCode,
          "busiType": '1',
          'data': {
            // 
            "ctmName": this.tableData.ctmName,
            "ctmIdCard": this.tableData.ctmIdCard,
            "userAddress": this.tableData.userAddress,
            "applyReason": this.tableData.applyReason,
            "predictWater": Number(this.tableData.predictWater),
            "predictUser": Number(this.tableData.predictUser),

            "operator": this.tableData.operator,
            "operatorPhone": this.tableData.operatorPhone,
            "applyCount": Number(this.tableData.applyCount),
            "accessory": [],
          }
        },
      }
      // 
      if (this.contentShowD) {
        params.data.data.accessory = [
          {
            "fileId": "",
            "type": "",
            "url": _this.fileUrlList[0]
          },
          {
            "fileId": "",
            "type": "",
            "url": _this.fileUrlList[1]
          },
          {
            "fileId": "",
            "type": "",
            "url": _this.fileUrlList[2]
          }
        ]
      }
      // 
      if (this.bankShow) {
        params.data.data.bank = {
          // 
          // "accountType": this.bank.accountType,
          // "payType": this.bank.payType,
          "openBank": this.bank.openBank,
          "accountName": this.bank.accountName,
          "bankNo": this.bank.bankNo,
        }
      } else {
        params.data.data.bank = null
      }
      // 
      if (this.invoiceShow) {
        params.data.data.invoice = {
          // 
          "invoiceType": this.invoice.invoiceType,
          "unitName": this.invoice.unitName,
          "identifiNum": this.invoice.identifiNum,
          "contactAddr": this.invoice.contactAddr,
          "mobile": this.invoice.mobile,
          "invOpenBank": this.invoice.invOpenBank,
          "invAccountNo": this.invoice.invAccountNo,
        }
      } else {
        params.data.data.invoice = null
      }
      // 
      if (_this.fileUrlList[3] !== undefined) {
        params.data.data.accessory.push({
          "fileId": "",
          "type": "",
          "url": _this.fileUrlList[3]
        })
      }
      // bug
      for (let i = 0; i < params.data.data.accessory.length; i++) {
        if (!('url' in params.data.data.accessory[i])) {
          return
        }
      }
      // 
      if (_this.$route.query.isModify == 1) {
        params.data.source = _this.$route.query.source
        params.data.busiId = _this.$route.query.busiId
        for (let i = 0; i < _this.initFileList.length; i++) {
          // fileId
          if ('fileId' in _this.initFileList[i]) {
            params.data.data.accessory[i].fileId = _this.initFileList[i].fileId
          } else {
            params.data.data.accessory[i].fileId = ''
          }

        }

      } else if (_this.$route.query.isModify == 2) {// 
        params.data.openId = localStorage.getItem('openId')
      }
      var url = ''
      if (_this.$route.query.isModify == 1) {
        url = '/myWater/busiUpdate'// 
      } else {
        url = '/myWater/waterApply'// 
      }
      this.$api.fetch({
        params: params,//
        apiUrl: url,//
      }).then(res => {
        Toast.success({
          message: '',
          position: 'middle',
          duration: 3000
        });
        setTimeout(() => {
          _this.moduleAccess(_this.tableData.groupCode)
          _this.tableData = {}
          if (_this.$route.query.isModify == 1) {
            _this.$router.push({ path: '/progressQuery' })
          } else {
            // _this.$router.push({ path: '/businesshand' })
            _this.$router.push({ path: '/progressQuery' })
          }
        }, 3000);
      })
    },
    // 
    sure(val) {
      if (typeof (val[2]) == "undefined" || val[2].code == '') {
        this.tableData.groupCode = val[1].code.slice(2)
        this.tableData.groupName = val[0].name + val[1].name
      } else {
        this.tableData.groupCode = val[2].code.slice(4);
        this.tableData.groupName = val[0].name + val[1].name + val[2].name
      }
      this.tableData.groupName = this.tableData.groupName.replace('*', '')
      this.relationShow = false
    },
    // 
    exit() {
      this.relationShow = false
    },
    // 
    getArrList2() {
      let _this = this
      let params = {
        "data": {
          openId: localStorage.getItem('openId')
        },
      }
      _this.$api.fetch({
        params,//
        apiUrl: '/common/wAreaTree',//
      }).then(res => {
        _this.areaList.province_list = res.provinceMap;//
        _this.areaList.city_list = res.cityMap;//
        _this.areaList.county_list = res.areaMap;//
        _this.getInvoiceType()
        _this.getBindData()
      })
    },
    // 
    getBindData() {
      let _this = this
      let params = {
        "data": {},
      }
      _this.$api.fetch({
        params,//
        apiUrl: '/common/getUserInfo',//
      }).then(res => {

        let bindData = res.bwcList
        if (bindData.length > 0) {
          _this.tableData.groupCode = bindData[0].groupCode
          _this.tableData.groupName = bindData[0].groupName

          let incode = _this.tableData.groupCode;
          let countylist = _this.areaList.county_list;//
          let citylist = _this.areaList.city_list;//
          let areanum = _this.areaNum;
          _this.areaNum = _this.common.companyTree(incode,countylist,citylist,areanum);
        } else {
          _this.getGeolocation()
        }
      })
    },
    // 
    getSpaceNum(longitude, latitude) {
      let _this = this
      let params = {
        "data": {
          'longitude': longitude,
          'latitude': latitude,
        },
      }
      _this.$api.fetch({
        params,//
        apiUrl: '/common/getShortestComp',//
      }).then(res => {
        _this.$set(_this.tableData, 'groupCode', res.groupCode)
        _this.$set(_this.tableData, 'groupName', res.groupArea)
      })
    },
    // 
    getGeolocation() {
      let _this = this
      var map = new AMap.Map('container', {
        resizeEnable: true
      });
      AMap.plugin('AMap.Geolocation', function () {
        var geolocation = new AMap.Geolocation({
          enableHighAccuracy: true,//:true
          timeout: 10000,          //105s
          buttonPosition: 'RB',    //
          buttonOffset: new AMap.Pixel(10, 20),//Pixel(10, 20)
          zoomToAccuracy: true,   //

        });
        map.addControl(geolocation);
        geolocation.getCurrentPosition(function (status, result) {
          if (status == 'complete') {
            _this.getSpaceNum(result.position.lng, result.position.lat)
          } else {
            // _this.onError(result)
          }
        });
      });
    },
    // 
    showAddressChuang() {
      if (this.isReadyonly) {
        return
      }
      this.relationShow = true
    },

    // 
    goBackB() {
      this.title = ''
      this.contentShowA = true
      this.contentShowB = false
      this.contentShowC = false
      this.contentShowD = false
      this.bankPhotoShow = true
    },
    // 
    goBackC() {
      if (this.bankShow) {
        this.title = ''
        this.contentShowA = false
        this.contentShowB = true
        this.contentShowC = false
        this.contentShowD = false
        this.bankPhotoShow = true
      } else {
        // 
        //  window (0,0)
        window.scrollTo(0, 0)
        setTimeout(() => {
          Dialog.confirm({
            message: '',
            confirmButtonText: '',
            cancelButtonText: '',
          }).then(() => {
            this.title = ''
            this.contentShowA = false
            this.contentShowB = true
            this.contentShowC = false
            this.contentShowD = false
            this.bankPhotoShow = true
            this.bankShow = true
          }).catch(() => {
            this.goBackB()
            this.bankShow = false
            this.bankPhotoShow = false
          });
        }, 1000);
      }

      // this.title = ''
      // this.contentShowA = false
      // this.contentShowB = true
      // this.contentShowC = false
      // this.contentShowD = false
    },
    // 
    goBackD() {
      if (this.invoiceShow) {
        this.title = ''
        this.contentShowA = false
        this.contentShowB = false
        this.contentShowC = true
        this.contentShowD = false
      } else {
        setTimeout(() => {
          Dialog.confirm({
            message: '',
            confirmButtonText: '',
            cancelButtonText: '',
          }).then(() => {
            this.title = ''
            this.contentShowA = false
            this.contentShowB = false
            this.contentShowC = true
            this.contentShowD = false

            this.invoiceShow = true
          }).catch(() => {
            this.goBackC()
            this.invoiceShow = false
          });
        }, 1000);
      }

      // this.title = ''
      // this.contentShowA = false
      // this.contentShowB = false
      // this.contentShowC = true
      // this.contentShowD = false
    },
    // 
    goBackE() {
      this.title = ''
      this.contentShowA = false
      this.contentShowB = false
      this.contentShowC = false
      this.contentShowD = true
      this.btnShow = true
      if (this.$route.query.isModify != 0) {
        this.isReadyonly = false
      }
    },
    // 
    nextA() {
      if (this.tableData.groupName === '') {
        Toast('');
        return
      } else if (this.tableData.ctmName === '') {
        Toast('');
        return
      } else if (this.tableData.operator === '') {
        Toast('');
        return
      } else if (this.tableData.operatorPhone === '') {
        Toast('');
        return
      }
      else if(!/^1([38]\d|5[0-35-9]|7[3678])\d{8}$/.test(this.tableData.operatorPhone)){
        Toast('');
        return
      }
      else if (this.tableData.applyCount === '') {
        Toast('');
        return
      } else if (this.tableData.predictWater === '') {
        Toast('');
        return
      } else if (this.tableData.predictUser === '') {
        Toast('');
        return
      } else if (this.tableData.ctmIdCard === '') {
        Toast('');
        return
      }
       else if (!/^[1-9]\d{5}(18|19|([23]\d))\d{2}((0[1-9])|(10|11|12))(([0-2][1-9])|10|20|30|31)\d{3}[0-9Xx]$/.test(this.tableData.ctmIdCard)) {
        Toast('');
        return
      }
      else if (this.tableData.userAddress === '') {
        Toast('');
        return
      }

      if (this.bankShow) {
        this.title = ''
        this.contentShowA = false
        this.contentShowB = true
        this.bankPhotoShow = true;
        this.contentShowC = false
        this.contentShowD = false
      } else {
        // 
        //  window (0,0)
        window.scrollTo(0, 0)
        setTimeout(() => {
          Dialog.confirm({
            message: '',
            confirmButtonText: '',
            cancelButtonText: '',
          }).then(() => {
            this.title = ''
            this.contentShowA = false
            this.contentShowB = true
            this.bankPhotoShow = true
            this.contentShowC = false
            this.contentShowD = false

            this.bankShow = true
          }).catch(() => {
            this.contentShowB = false
            this.nextB()
            this.bankShow = false
            this.bankPhotoShow = false
          });
        }, 1000);
      }

    },
    // 
    nextB() {
      if (this.contentShowB) {
        if (this.bank.openBank === '') {
          Toast('');
          return
        } else if (this.bank.accountName ==='') {
          Toast('');
          return
        } else if (this.bank.bankNo === '') {
          Toast('');
          return
        }
         else if (!/^([1-9]{1})(\d{14}|\d{18})$/.test(_this.bank.bankNo)) {
          Toast('');
          return
        }
      }


      if (this.invoiceShow) {
        this.title = ''
        this.contentShowA = false
        this.contentShowB = false
        this.bankPhotoShow = true;
        this.contentShowC = true
        this.contentShowD = false
      } else {
        // 
        //  window (0,0)
        window.scrollTo(0, 0)
        setTimeout(() => {
          Dialog.confirm({
            message: '',
            confirmButtonText: '',
            cancelButtonText: '',
          }).then(() => {
            this.title = ''
            this.contentShowA = false
            this.contentShowB = false
            this.contentShowC = true
            this.contentShowD = false

            this.invoiceShow = true
          }).catch(() => {
            this.contentShowC = false
            this.nextC()
            this.invoiceShow = false
          });
        }, 1000);
      }
    },
    // ()
    nextC() {
      if (this.contentShowC) {
        if (this.invoice.invoiceType === '') {
          Toast('');
          return
        } else if (this.invoice.unitName === '') {
          Toast('');
          return
        } else if (this.invoice.identifiNum === '') {
          Toast('');
          return
        } else if (this.invoice.mobile === '') {
          Toast('');
          return
        }
         else if (!/^1([38]\d|5[0-35-9]|7[3678])\d{8}$/.test(_this.invoice.mobile)) {
          Toast('');
          return
        }
        else if (this.invoice.invOpenBank === '') {
          Toast('');
          return
        } else if (this.invoice.invAccountNo === '') {
          Toast('');
          return
        } else if (this.invoice.contactAddr === '') {
          Toast('');
          return
        }
         else if (!/^([1-9]{1})(\d{14}|\d{18})$/.test(_this.invoice.invAccountNo)) {
          Toast('');
          return
        }
        else {
          this.title = ''
          this.contentShowA = false
          this.contentShowB = false
          this.contentShowC = false
          this.contentShowD = true
        }
      } else {
        this.title = ''
        this.contentShowA = false
        this.contentShowB = false
        this.contentShowC = false
        this.contentShowD = true
      }
    },
    // 
    nextD() {
      if (this.fileList1.length == 0 || this.fileList2.length == 0 || this.fileList3.length == 0) {
        // || this.fileList4.length == 0
        Toast('');
        return
      }
      if (this.bankPhotoShow && this.fileList4.length == 0) {
        Toast('');
        return
      }
      this.title = ''
      this.contentShowA = true
      this.contentShowD = true
      this.isQuery = true;//
      this.btnShow = false
      if (this.$route.query.isModify != 0) {
        this.isReadyonly = true
        this.icon = ''
        this.iconType = '';
      }


      if (this.bankShow) {
        this.contentShowB = true
        this.bankPhotoShow = true;
      } else {
        this.contentShowB = false
      }
      if (this.invoiceShow) {
        this.contentShowC = true
      } else {
        this.contentShowC = false
      }
    },
    // 
    cancel() {
      if (this.$route.query.isModify !== 0) {
        Dialog.confirm({
          message: ''
        }).then(() => {
          // 
          this.$router.push('/businesshand')
        }).catch(() => {
          return
        });
      } else {
        // 
        this.$router.push('/progressQuery')
      }

    },
  }
}
</script>

