      border: none;
      // font-family: PingFang_Medium;
      // color: #000000;
    }
  }
  .list li + li .item-content:before {
    background-color: white;
  }
  .second_content {
    display: inline-block;
    width: 35%;
    border-bottom: #dcdcdc solid 1px;
    padding-left: 0.75rem;
    span {
      font-size: 13px;
      color: #989898;
      // font-family: PingFang_Bold;
      font-weight: 600;
    }
    input {
      width: 90%;
      font-size: 15px;
      border: none;
      // font-family: PingFang_Medium;
      // color: #000000;
    }
  }
  .second_content:first-child {
    width: 25%;
  }
  .Third_content {
    display: inline-block;
    width: 100%;
    border-bottom: 1px solid #dcdcdc;
    padding-left: 0.75rem;
    span {
      font-size: 13px;
      color: #888888;
      // font-family: PingFang_Regular;
      font-weight: 300;
    }
    input {
      width: 90%;
      font-size: 15px;
      border: none;
      // font-family: PingFang_Medium;
    }
  }
  .add_box {
    position: absolute;
    width: 20%;
    top: 77.5%;
    right: 6%;
    height: 8%;
    z-index: 2000;
    .midle-bar {
      float: right;
    }
  }
  .content-padded {
    margin: 0;
    .img_content {
      height: 9rem;
    }
    // overflow-y: hidden;
  }
  .col-100 {
    .list {
      margin: 0;
      .list-title {
        padding: 0;
      }
    }
    .form-list .item-title.label {
      width: 40%;
    }
    .form-list .item-content img {
      position: absolute;
      top: 0.2rem;
      left: 50%;
    }
  }
}
</style>



